@import './fonts-and-colors.scss';

// Reset
* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Ubuntu', sans-serif;
    font-weight: 300;
}

h1 {
    font-weight: 700;
    color: $primary-font-color;
    font-size: 2.5rem;
}

h2 {
    font-weight: 400;
    color: $primary-font-color;
}

h3 {
    font-weight: 400;
    color: $primary-font-color;
}

p {
    font-size: 1.15rem;
    color: $secondary-font-color;
}

.link-set {
    .icon {
        transition: all 150ms ease-in-out;

        &:hover {
            scale: 1.25;
        }
    }
}

button {
    display: inline-block;
    padding: 1rem 2rem;
    font-size: 1rem;
    text-align: center;
    text-decoration: none;
    letter-spacing: 1px;
    border: none;
    background-color: rgb(0, 128, 128);
    color: white;
    cursor: pointer;
    transition: background-color 150ms ease;

    &:hover {
        background-color: #2980b9;
        background-color: rgb(74, 140, 194);
    }
}

header {
    display: flex;
    position: fixed;
    z-index: 1000;
    width: 100%;
    align-items: center;
    flex-direction: column;
}

.container {
    display: flex;
    flex-direction: column;
    width: 100%;

    .triangle {
        display: block;
        width: 100%;
    }

    .content-box {
        display: flex;
        justify-content: center;
        width: 100%;
        min-height: 100px;
    }
}

.head-img-container {
    display: flex;
    justify-content: center;
    height: 600px;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;

    .head-content-box {
        display: flex;
        align-items: center;
        height: 100%;
        width: 70%;
    }
}

